import React, { useEffect, useState } from "react";
import logo from "../../assets/Header_Logo.png";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import styles from "./HeaderMain.module.scss";
import { logout, selectIsAuth } from "../../redux/slices/auth";

const HeaderMain = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const isAuth = useSelector(selectIsAuth);
  const [emailLink, setEmailLink] = useState("");

  const onClickLogout = () => {
    if (window.confirm("   ?")) {
      dispatch(logout());
      navigate("/login");
    }
  };

  useEffect(() => {
    const date = new Date().toLocaleString("ru-RU", {
        hour: "2-digit",
        minute: "2-digit",
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
      });

      const subject = `[ ${date}][main]  `;
      const body = ` !\n\n : (,  )\n:  1  4 (1 - , 4 - )\n   : (    )\n\n___\n ,\n    `;

      const mailtoLink = `mailto:vcp-tech-sup@mail.ru?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
      setEmailLink(mailtoLink);
  }, []);
  
  return (
    <header>
      <img src={logo} alt="BIA Logo" draggable="false"/>
      <nav>
        <ul>
          <li className={styles.cur}>
            <Link to="/"></Link>
          </li>
          <li>
            <a target="_blank" href="https://bia-tech.ru/?utm_referrer=https%3A%2F%2Fwww.google.com%2F"> </a>
          </li>
          <li>
            <a href={emailLink}></a>
          </li>
        </ul>
      </nav>
      <Link to="/login" className={styles.button}>
        
      </Link>
    </header>
  );
};

export default HeaderMain;

