import React, { useEffect, useState } from "react";
import logo from "../../assets/Header_Logo.png";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import styles from "./HeaderUser.module.scss";
import { logout } from "../../redux/slices/auth";
import { jwtDecode } from "jwt-decode";
import axios from "../../axios";

const HeaderUser = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();

  const [userId, setUserId] = useState("");
  const [userInfo, setUserInfo] = useState({ name: "", surname: "", patro: "" });
  const [emailLink, setEmailLink] = useState("");

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      const decodedToken = jwtDecode(token);
      const userId = decodedToken._id;
      console.log(userId);
      setUserId(userId);

      //        
      axios.get(`/user-get-fullname/${userId}`)
        .then(response => {
          setUserInfo(response.data);
        })
        .catch(error => {
          console.error("    :", error);
        });
    }
  }, []);

  useEffect(() => {
  if (userInfo.name && userInfo.surname && userInfo.patro) {
      const date = new Date().toLocaleString("ru-RU", {
        hour: "2-digit",
        minute: "2-digit",
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
      });

      const subject = `[ ${date}][${userId}]    ${userInfo.surname} ${userInfo.name} ${userInfo.patro}`;
      const body = ` !\n\n : (,  )\n:  1  4 (1 - , 4 - )\n   : (    )\n\n___\n ,\n    \n${userInfo.surname} ${userInfo.name} ${userInfo.patro}`;

      const mailtoLink = `mailto:vcp-tech-sup@mail.ru?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
      setEmailLink(mailtoLink);
    }
  }, [userInfo, userId]);

  const onClickLogout = () => {
    if (window.confirm("   ?")) {
      dispatch(logout());
      navigate("/login");
    }
  };

  const isActive = (path) => location.pathname === path;

  return (
    <header className={styles.header}>
      <img src={logo} alt="BIA Logo" draggable="false"/>
      <nav>
        <ul>
          <li className={isActive("/userhome") ? styles.active : ""}>
            <Link to="/userhome"></Link>
          </li>
          <li className={isActive("/userprofile") || isActive("/usertask") || isActive("/userresult") ? styles.active : ""}>
            <Link to="/userprofile"></Link>
          </li>
          <li>
            <a href={emailLink}></a>
          </li>
        </ul>
      </nav>
      <button onClick={onClickLogout} className={styles.button}>
        
      </button>
    </header>
  );
};

export default HeaderUser;
